import React, { useState, useEffect } from "react";

// import Core UI
import {
  CCard,
  CCol,
  CRow,
  CCardHeader,
  CCardBody,
  CButton,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CForm,
  CFormGroup,
  CLabel,
  CInput,
  CFormText,
  CHeader,
  CSelect,
  CTextarea,
} from "@coreui/react";
import MaterialTable from "material-table";

import CIcon from "@coreui/icons-react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import SVCMasterWil from "../services-wil";
import swal from "sweetalert";
import { useDispatch, useSelector } from "react-redux";
import {
  setDataComboProv,
  setDataDropdownKabupatenAction,
  GeKecamatanByIdKabupatenAction,
  setFormKecAction,
  prosesKecamatanAddAction,
  GetKecamatanByIdKecamatanAction,
  prosesKecamatanEditAction,
  ProsesDeleteKecamatanAction,
} from "src/common/redux/action";

import Swal from "sweetalert2";

const Kecamatan = () => {
  const { dataComboProv, dataComboKabupaten } = useSelector(
    (state) => state.dropdownReducer
  );

  const { DataKecamatanFilter, modalKec, formKecamatan } = useSelector(
    (state) => state.kecamatanReducer
  );
  const {
    IdKecamatan,
    IdKabupaten,
    Kode,
    Nama,
    Alamat,
    Longitude,
    Latitude,
  } = formKecamatan;
  const [changeIdProv, setChangeIdProv] = useState(0);
  const [changeFilterKab, setChangeFilterKab] = useState(0);
  const [stateUpdate, setStateUpdate] = useState(false);
  const dispatch = useDispatch();
  const getProv = () => dispatch(setDataComboProv("DATA_CMB_PROV"));
  const setChangeProv = (event, val) => {
    const changeIdProvNew = { ...changeIdProv };
    setChangeIdProv(val);
    dispatch(setDataDropdownKabupatenAction("DATA_CMB_KAB", val));
  };

  const showModal = () => dispatch({ type: "MODAL_KEC", payload: !modalKec });

  const setChangeKabupatenAction = (event, value) => {
    const changeFilterKabNew = { ...changeFilterKab };
    setChangeFilterKab(value);
  };

  const filterKecamatan = () => {
    if (changeFilterKab == 0) {
      Swal.fire("Gagal", "Kabupaten belum dipilih", "error");
    } else {
      dispatch(
        GeKecamatanByIdKabupatenAction("GET_KECAMATANS", changeFilterKab)
      );
    }
  };

  const handleModalKecamatan = (type, idKec) => {
    if (type === "add_kecamatan") {
      setStateUpdate(false);
      showModal();
      dispatch({ type: "RESET_FORM_KEC" });
    } else {
      setStateUpdate(true);
      showModal();
      dispatch(GetKecamatanByIdKecamatanAction(idKec));
    }
  };

  const prosesAdd = () => dispatch(prosesKecamatanAddAction(formKecamatan));

   const prosesUpdate = () => dispatch(prosesKecamatanEditAction(formKecamatan));
  const prosesDelete = (idKec, idKab, nama) =>
    dispatch(ProsesDeleteKecamatanAction(idKec, idKab, nama));

  const columns = [
    {
      title: "Aksi",
      field: "internal_action",
      cellStyle: {
        backgroundColor: "#039be5",
        color: "#FFF",
      },
      headerStyle: {
        backgroundColor: "#039be5",
      },
      render: (rowData) => {
        return (
          <div className="row">
            <button
              type="button"
              className="btn btn-sm btn-dark mr-2 ml-4"
              onClick={() => handleModalKecamatan("edit", rowData.idKecamatan)}
            >
              <CIcon name="cil-pencil" /> Update
            </button>

            <button
              type="button"
              className="btn btn-sm btn-danger"
              onClick={() =>
                prosesDelete(
                  rowData.idKecamatan,
                  rowData.idKabupaten,
                  rowData.nama
                )
              }
            >
              <CIcon name="cil-drop" /> Hapus
            </button>
          </div>
        );
      },
    },
    { title: "Kode", field: "kode" },
    { title: "Nama Kecamatan", field: "nama" },
    { title: "Alamat", field: "alamat" },
    { title: "Longitude", field: "longitude" },
    { title: "Latitude", field: "latitude" },
  ];

  useEffect(() => {
    getProv();
  }, []);
  return (
    <CRow>
      <CCol md="12" xl="12" xs="12">
        <CCard>
          <CCardHeader>
            <CCol md="3">
              <CButton
                block
                color="primary"
                onClick={() => handleModalKecamatan("add_kecamatan")}
              >
                <FontAwesomeIcon icon="plus" />
                Tambah
              </CButton>
            </CCol>
          </CCardHeader>
          <CCardBody>
            <CCol md="12">
              <CRow>
                <CCol md="2" className="pt-2">
                  <CLabel> Provinsi</CLabel>
                </CCol>
                <CCol md="3" className="mb-2">
                  <CSelect
                    custom
                    name="select_filter_provinsi"
                    id="filter_provinsi"
                    onChange={(event) =>
                      setChangeProv("IdProvinsi", event.target.value)
                    }
                  >
                    <option value="0"> -- Pilih Provinsi --</option>
                    {dataComboProv.map((v) => (
                      <option key={v.idProvinsi} value={v.idProvinsi}>
                        {" "}
                        {v.nama}
                      </option>
                    ))}
                  </CSelect>
                </CCol>
                <CCol md="2">
                  <CLabel> Kabupaten </CLabel>
                </CCol>
                <CCol md="3">
                  <CSelect
                    custom
                    name="filter_kabupaten"
                    id="filter_kabupaten"
                    onChange={(event) =>
                      setChangeKabupatenAction(
                        "IdKabupaten",
                        event.target.value
                      )
                    }
                  >
                    <option value="0"> Pilih Kabupaten</option>
                    {dataComboKabupaten.length > 0
                      ? dataComboKabupaten.map((v) => (
                          <option key={v.idKabupaten} value={v.idKabupaten}>
                            {" "}
                            {v.nama}
                          </option>
                        ))
                      : null}
                  </CSelect>
                </CCol>
                <CCol md="2">
                  <CButton className="btn btn-info" onClick={filterKecamatan}>
                    <FontAwesomeIcon icon="search" /> Filter
                  </CButton>
                </CCol>
              </CRow>
            </CCol>

            <MaterialTable
              title=""
              options={{
                headerStyle: {
                  backgroundColor: "#01579b",
                  color: "#FFF",
                },
                filtering: true,
              }}
              columns={columns}
              data={DataKecamatanFilter.length > 0 ? DataKecamatanFilter : []}
            />
          </CCardBody>
        </CCard>
      </CCol>
      // inisialisasi modal
      <CModal show={modalKec} onClose={showModal} size="lg">
        <CModalHeader closeButton>
          <CModalTitle>Form Kecamatan</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <CForm className="form-horizontal">
            <CFormGroup>
              <CLabel htmlFor="nf-provinsi"> Provinsi </CLabel>
              <CSelect
                onChange={(event) =>
                  setChangeProv("IdProvinsi", event.target.value)
                }
              >
                <option value="0"> Pilih Provinsi</option>
                {dataComboProv.map((v) => (
                  <option key={v.idProvinsi} value={v.idProvinsi}>
                    {" "}
                    {v.nama}
                  </option>
                ))}
              </CSelect>
            </CFormGroup>
            <CFormGroup>
              <CLabel htmlFor="nf-kabupaten"> Kabupaten</CLabel>
              <CSelect
                custom
                name="form_select_kabupten"
                id="form_select_lkabupaten"
                value={IdKabupaten}
                onChange={(event) =>
                  dispatch(setFormKecAction("IdKabupaten", event.target.value))
                }
              >
                <option value="0"> Pilih Kabupaten </option>
                {dataComboKabupaten.length > 0
                  ? dataComboKabupaten.map((v) => (
                      <option key={v.idKabupaten} value={v.idKabupaten}>
                        {" "}
                        {v.nama}
                      </option>
                    ))
                  : null}
              </CSelect>
            </CFormGroup>
            <CFormGroup>
              <CLabel htmlFor="nf-kode"> Kode</CLabel>
              <CInput
                type="text"
                value={Kode}
                onChange={(event) =>
                  dispatch(setFormKecAction("Kode", event.target.value))
                }
              ></CInput>
            </CFormGroup>
            <CFormGroup>
              <CLabel htmlFor="nf-nama"> Nama</CLabel>
              <CInput
                value={Nama}
                type="text"
                onChange={(event) =>
                  dispatch(setFormKecAction("Nama", event.target.value))
                }
              ></CInput>
            </CFormGroup>
            <CFormGroup>
              <CLabel htmlFor="ng-alamat"> Alamat</CLabel>
              <CTextarea
                value={Alamat}
                onChange={(e) =>
                  dispatch(setFormKecAction("Alamat", e.target.value))
                }
              ></CTextarea>
            </CFormGroup>
            <CFormGroup>
              <CLabel htmlFor="latitude"> Latitude</CLabel>
              <CInput
                type="text"
                value={Latitude}
                onChange={(e) =>
                  dispatch(setFormKecAction("Latitude", e.target.value))
                }
              ></CInput>
            </CFormGroup>
            <CFormGroup>
              <CLabel htmlFor="longitude"> Longitude</CLabel>
              <CInput
                type="text"
                value={Longitude}
                onChange={(e) =>
                  dispatch(setFormKecAction("Longitude", e.target.value))
                }
              ></CInput>
            </CFormGroup>
          </CForm>
        </CModalBody>
        <CModalFooter>
          <CButton color="primary" onClick={stateUpdate ? prosesUpdate : prosesAdd}>{stateUpdate ? "Update" : "Tambah"}</CButton>
          <CButton color="secondary" onClick={showModal}>
            Cancel
          </CButton>
        </CModalFooter>
      </CModal>
    </CRow>
  );
};

export default Kecamatan;
